.plant-card .image-wrapper {
    display: flex;
    justify-content: space-between;
    justify-content: center;
}
.plant-card .image-wrapper img {
    width: 45%;
}

.plant-card {
    background-color: #FAF0F0;
    box-shadow: 
        0 1px 3px rgb(0, 0, 0, 0.12),
        0 1px 2px rgb(0, 0, 0, 0.24);
    transition: all 0.3s ease-in-out;
    padding: 5%;
    margin: 5% 15%;
    border-radius: 15px;
}

.plant-card:hover {
    box-shadow: 
    0 6px 12px rgb(0, 0, 0, 0.25);
}