@import url(https://fonts.googleapis.com/css2?family=Caudex:wght@400;700&family=Raleway:wght@300;400;600;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.nav h1 {
    font-family: 'Caudex', serif;
    margin: 0;
    font-size: xx-large;
}

h2 {
    font-family: 'Caudex', serif;
    margin: 0;
    font-size: xx-large
}

h3, h4, p, label, input, button {
    font-family: 'Raleway', sans-serif !important;
}

.nav {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    text-align: center;
    margin: 5%;
}

.nav h1 {
    -webkit-flex-grow: 1;
            flex-grow: 1;
    
}

.nav a {
    text-decoration: none;
    color: black;
}

.nav a:hover {
    text-decoration: none;
    color: grey;
    cursor: pointer; 
    -webkit-transform: rotate(360deg); 
            transform: rotate(360deg); 
    transition: all 0.6s ease-in-out 0s;
}
.image-search .image-search-form {
    background-color: #FAF0F0;
    /* border: 2px solid #C4C4C4; */
    display: -webkit-flex;
    display: flex;
    /* flex-direction: row; */
    -webkit-flex-flow: wrap;
            flex-flow: wrap;
    -webkit-justify-content: center;
            justify-content: center;
    text-align: justify;
    margin: 5% 15% 2.5% 15%;
    padding: 2.5% 5%;
    border-radius: 15px;
    box-shadow: 
        0 1px 3px rgb(0, 0, 0, 0.12),
        0 1px 2px rgb(0, 0, 0, 0.24);
}

.image-search .image-search-form input {
    width: 75%;
    
}
.image-card .image-wrapper {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
.image-card .image-wrapper img {
    width: 45%;
}

.image-card {
    background-color: #FAF0F0;
    box-shadow: 
        0 1px 3px rgb(0, 0, 0, 0.12),
        0 1px 2px rgb(0, 0, 0, 0.24);
    transition: all 0.3s ease-in-out;
    padding: 5%;
    margin: 5% 15%;
    border-radius: 15px;
}

.image-card:hover {
    box-shadow: 
    0 6px 12px rgb(0, 0, 0, 0.25);
}

footer {
    margin: 5% 0 0 0;
}

.confirm-dialog {
    margin: 5% 0;
    padding: 55% 0;
    width: 100px;
    text-align: center;
    -webkit-backdrop-filter: blur(15px);
            backdrop-filter: blur(15px);
    box-shadow: 
    0 1px 3px rgb(0, 0, 0, 0.12),
    0 1px 2px rgb(0, 0, 0, 0.24);
}

.confirm-dialog .actions, .confirm-dialog .content {
    background-color: #fff;
    margin: 0 25%;
    padding: 2.5%;
}
.slideUpPanel {
  cursor: pointer;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.modal {
  position: fixed;
  bottom: -85%;
  width: 100%;
  height: 90%;
  left: 0;
  transition: all 0.5s ease-out;
  z-index: 10;
}

.text {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding: 10%;
}

.modal.show {
  bottom: 0;
}

.overlay {
  background-color: rgba(230, 230, 230, 0.55);
  transition: all 0.5s ease-out;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  position: fixed;
  display: none;
  z-index: 5;
}

.overlay.show {
  display: block;
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  transition: all 0.5s ease-out;
}

